"use client"; // این کامپوننت حالا state و هوک مدیریت می‌کند

import React, { ReactNode } from "react";
import { YadakchiAdminLayout } from "@my-ds/design-system";

// ۱. تمام وابستگی‌های Next.js در اینجا قرار دارند
import Link from "next/link";
import { usePathname } from "next/navigation";

export default function HomePage() {
  // ۲. هوک‌ها در اینجا فراخوانی می‌شوند
  const pathname = usePathname();
  
  // ۳. تابع render prop در اینجا تعریف می‌شود
  const renderNextLink = (href: string, children: ReactNode) => {
    return <Link href={href}>{children}</Link>;
  };

  return (
    // ۴. مقادیر محاسبه شده به عنوان props به لی‌آوت پاس داده می‌شوند
    <YadakchiAdminLayout
      currentPath={pathname}
      renderLink={renderNextLink}
    >
      <h1 className="text-2xl font-semibold">داشبورد</h1>
      <p className="mt-2">به پنل مدیریت خوش آمدید.</p>
      <div className="mt-8 p-4 bg-white dark:bg-slate-800 rounded-lg shadow">
        <p>این یک کارت نمونه برای نمایش محتوای صفحه است.</p>
      </div>
    </YadakchiAdminLayout>
  );
}